<template>
  <div class="text-center">
    <v-dialog v-model="showModel" persistent max-width="600px">
      <v-card :disabled="isRequest" :loading="isRequest">
        <v-card-title>
          <span class="text-h5">Agregar Tanda</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-row>
            <v-container>
              <v-row class="mt-0">
                <v-col cols="12" lg="12" sm="6" md="4">
                  <v-form ref="form">
                    <v-row justify="space-around">
                      <v-switch
                        inset
                        label="Estreno"
                        v-model="formData.estreno"
                      ></v-switch>
                      <v-switch
                        inset
                        label="Doblada"
                        v-model="formData.doblada"
                      ></v-switch>
                    </v-row>
                    <v-text-field
                      label="Titulo"
                      outlined
                      v-model="formData.title"
                      required
                      :rules="requiredParam"
                    >
                    </v-text-field>
                    <v-file-input
                      counter
                      show-size
                      truncate-length="10"
                      accept="image/*"
                      label="Portada"
                      outlined
                      @change="(e) => (formData.file = e)"
                      required
                      :rules="requiredParam"
                    ></v-file-input>
                    <v-text-field
                      v-model="formData.trailer"
                      label="Trailer, (link youtube)"
                      outlined
                    >
                    </v-text-field>
                    <v-textarea
                      v-model="formData.description"
                      outlined
                      label="Descripcion"
                      required
                      :rules="requiredParam"
                    ></v-textarea>
                  </v-form>
                </v-col>
              </v-row>
            </v-container>
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="toggleShowModel()">
            Cerrar
          </v-btn>
          <v-btn color="blue darken-1" text @click="saved()"> Guardar </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-btn
      fab
      dark
      small
      color="blue"
      fixed
      right
      bottom
      @click="toggleShowModel()"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </div>
</template>

<script>
import axios from "../../services/axios";
import LoadAnimation from "../loadAnimation";
export default {
  data: () => {
    return {
      requiredParam: [(v) => !!v || "Parametro es requerido"],
      showModel: false,
      isRequest: false,
      formData: {
        title: null,
        description: null,
        trailer: null,
        file: null,
        estreno: true,
        doblada: false,
      },
    };
  },
  methods: {
    toggleShowModel() {
      this.showModel = !this.showModel;
    },
    saved() {
      if (this.$refs.form.validate()) {
        let form = new FormData();

        form.append("title", this.formData.title);
        form.append("description", this.formData.description);
        form.append("trailer", this.formData.trailer);
        form.append("estreno", this.formData.estreno);
        form.append("doblada", this.formData.doblada);

        if (this.formData.file) {
          form.append("portada", this.formData.file);
        }

        this.isRequest = true;
        axios.post("/api/tandas", form).then((res) => {
          this.isRequest = false;
          this.showModel = false;
          if (res.data["status"] == 200) {
            this.loadTandas();
          }
        });
      }
    },
  },
  components: {
    LoadAnimation,
  },
  props: ["loadTandas"],
};
</script>