<template>
  <div class="text-center">
    <v-dialog v-model="showDialog" persistent max-width="600px">
      <v-card :disabled="isRequest" :loading="isRequest">
        <v-card-title>
          <span class="text-h5">Editar Tanda</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-row>
            <v-col cols="12" lg="12" sm="6" md="4">
              <v-form ref="form">
                <v-container>
                  <v-row justify="space-around">
                    <v-switch
                      inset
                      label="Estreno"
                      v-model="formData.estreno"
                    ></v-switch>
                    <v-switch
                      inset
                      label="Doblada"
                      v-model="formData.doblada"
                    ></v-switch>
                  </v-row>

                  <v-row>
                    <v-col cols="12" lg="12" sm="6" md="4">
                      <v-text-field
                        label="Titulo"
                        outlined
                        v-model="formData.title"
                        required
                        :rules="requiredParam"
                      >
                      </v-text-field>
                      <v-file-input
                        counter
                        show-size
                        truncate-length="10"
                        accept="image/*"
                        label="Portada"
                        outlined
                        @change="(e) => (formData.file = e)"
                      ></v-file-input>
                      <v-text-field
                        v-model="formData.trailer"
                        label="Trailer, (link youtube)"
                        outlined
                        required
                        :rules="requiredParam"
                      >
                      </v-text-field>
                      <v-textarea
                        v-model="formData.description"
                        outlined
                        label="Descripcion"
                        required
                        :rules="requiredParam"
                      ></v-textarea>
                    </v-col>
                  </v-row>
                </v-container>
              </v-form>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="toggleDialog">
            Cerrar
          </v-btn>
          <v-btn color="blue darken-1" text @click="saved()"> Guardar </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "../../services/axios";
export default {
  data: () => {
    return {
      requiredParam: [(v) => !!v || "Parametro es requerido"],
      showDialog: true,
      isRequest: false,
      formData: {
        title: null,
        description: null,
        trailer: null,
        file: null,
        estreno: true,
        doblada: false,
      },
    };
  },
  methods: {
    saved() {
      if (this.$refs.form.validate()) {
        let form = new FormData();
        form.append("id", this.item["id"]);

        form.append("title", this.formData.title);
        form.append("description", this.formData.description);
        form.append("trailer", this.formData.trailer);
        form.append("estreno", this.formData.estreno);
        form.append("doblada", this.formData.doblada);
        if (this.formData.file) form.append("portada", this.formData.file);
        this.isRequest = true;
        axios.put("/api/tandas", form).then((res) => {
          this.isRequest = false;
          if (res.data["status"] == 200) {
            this.toggleDialog();
            this.loadTandas();
          }
        });
      }
    },
  },
  created() {
    this.formData.title = this.item["title"];
    this.formData.description = this.item["description"];
    this.formData.trailer = this.item["ytlink"];
    this.formData.estreno = this.item["estreno"];
    this.formData.doblada = this.item["doblada"];
  },
  props: ["loadTandas", "toggleDialog", "item"],
};
</script>